<template>
  <div>
    <el-card class="box-card1">
      <span class="tittle">人员搜索:</span>
      <el-input
        placeholder="请输入"
        class="bigbox"
        v-model="findList.userName"
      ></el-input>
      <span class="tittle" style="margin-left: 30px">角色:</span>
      <el-input
        placeholder="请输入"
        style="margin-left: 20px; width: 300px; margin-top: 5px"
      ></el-input>
      <el-button
        type="primary"
        class="btn"
        size="medium"
        style="margin-left: 30px"
        @click="findfn"
        >查询</el-button
      >
    </el-card>
    <el-table :data="data" style="width: 100%" class="Table1">
      <el-table-column prop="date" label="序号" width="120" type="index">
      </el-table-column>
      <el-table-column prop="userName" label="人员名称" width="250">
      </el-table-column>
      <el-table-column prop="roleName" label="角色" width="200">
      </el-table-column>
      <el-table-column prop="mobile" label="联系电话" width="250">
      </el-table-column>
      <el-table-column prop="workCount" label="完成本单(本月)" width="250">
      </el-table-column>
      <el-table-column prop="progressTotal" label="进行中工单" width="250">
      </el-table-column>
      <el-table-column prop="cancelCount" label="拒绝工单(本月)" width="250">
      </el-table-column>
      <el-table-column prop="zip" label="操作" width="250">
        <template slot-scope="scope">
          <el-button
            size="mini"
            style="color: blue"
            @click="handleEdit(scope.$index, scope.row)"
            >查看详情</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      class="dialog_border"
      style="border-radius: 20px"
      title="人员详情"
      :visible.sync="dialogVisible"
      width="40%"
    >
      <div class="card_fromlist">
        <!-- 上 -->
        <div class="card_fromlist_title">
          <span>人员名称: {{formlist.userName}}</span>
          <span style="transform: translateX(-60px)">角色：{{formlist.roleName}}</span>
          <span style="transform: translateX(-100px)"
            >联系电话：{{formlist.mobile}}</span
          >
        </div>
        <div class="card_fromlist_title2">
          <span>负责区域：金燕龙办公楼</span>
        </div>
      </div>

      <!-- 表格 -->
      <el-table :data="gongdan" border style="width: 100% margin-top: 60px;">
         <el-table-column prop="date" label="" width="145">
        <template slot-scope>
        <div>本周</div>
      </template>

         </el-table-column>
        <el-table-column prop="total" label="总工单数" width="145"></el-table-column>
        <el-table-column prop="cancelCount" label="拒绝工单" width="145"></el-table-column>
        <el-table-column prop="workCount" label="完成工单" width="145"> </el-table-column>
        <el-table-column prop="progressTotal" label="进行中工单" width="145"> </el-table-column>
      </el-table>
    </el-dialog>
    <!-- 分页 -->
    <div class="block1">
      <span>共{{ totalCount }}条记录 第{{ pageIndex }}/{{ totalPage }}页</span>
      <el-row class="info1">
        <el-button type="info" class="bix1" @click="lastpage">上一页</el-button>
        <el-button type="info" @click.native="nextpage">下一页</el-button>
      </el-row>
    </div>
  </div>
</template>

<script>
import { getworlist, findlist, getgongdan } from '@/api/PEOPLE'
export default {
  data () {
    return {
      tableData: [{
        date: '2016-05-02',
        name: '王小虎',
        address: '上海市普陀区金沙江路 1518 弄'
      }, {
        date: '2016-05-04',
        name: '王小虎',
        address: '上海市普陀区金沙江路 1517 弄'
      }, {
        date: '2016-05-01',
        name: '王小虎',
        address: '上海市普陀区金沙江路 1519 弄'
      }, {
        date: '2016-05-03',
        name: '王小虎',
        address: '上海市普陀区金沙江路 1516 弄'
      }],
      dialogVisible: false,
      worklist: {
        start: '',
        end: ''
      },
      pageIndex: '',
      pageSize: '',
      totalCount: '',
      totalPage: '',
      findList: {
        pageIndex: '',
        pageSize: '',
        userName: ''
      },
      formlist: {},
      gongdan: []
    }
  },
  created () {
    this.getworokist()
  },

  methods: {
    async  handleEdit (index, row) {
      this.dialogVisible = true
      // console.log(row)
      this.formlist = JSON.parse(JSON.stringify(row))
      const res = await getgongdan({
        userId: row.userId,
        start: '2022-01-01 00:00:00',
        end: '2022-12-01 23:59:59'
      })
      // console.log(res)
      this.gongdan.push(res)
      console.log(this.gongdan)
    },
    async findfn () {
      const res = await findlist(this.findList)
      this.data = res.currentPageRecords
      this.pageIndex = res.pageIndex
      this.pageSize = res.pageSize
      this.totalCount = res.totalCount
    },
    async getworokist () {
      const { currentPageRecords, pageIndex, totalPage, totalCount } =
        await getworlist({
          pageSize: this.pageSize,
          pageIndex: this.pageIndex
        })
      this.data = currentPageRecords
      // 分页计数
      this.totalCount = totalCount // 总条数
      this.pageIndex = pageIndex // 当前页
      this.totalPage = totalPage // 总页数
      console.log(this.data)
    },
    lastpage () {
      if (this.pageIndex === 0) {
        return
      }
      this.pageIndex = Number(this.pageIndex) - 1
      this.getworokist()
    },
    nextpage () {
      console.log(this.totalPage, this.pageIndex)
      if (this.pageIndex === this.totalPage) {
        return
      }
      this.pageIndex = Number(this.pageIndex) + 1
      this.getworokist()
    }
  }
}
</script>

